import React, {Component, Fragment} from 'react';
import {List, Button, Col,Card} from 'antd';


class LightningList extends Component {

  handleRemove = index => {
    console.log(index)
    this.props.handleRemoveLightning(index)
  }
  handleSubmitList=(lightningList)=>{
    // e.preventDefault();
    console.log("clik",lightningList)
    this.props.handleSubmitList(lightningList);
  }

  render() {
    return (
      <Fragment>
        <Card>
        <List
          bordered
          dataSource={this.props.localLightningList}
          renderItem={(item, i) => (<List.Item>
            <Col span={12}> {item.subject} </Col>
            <Col span={4}> {item.duration} min</Col>
            <Col span={4} offset={0}>
              <Button onClick={e => this.handleRemove(i)}>Delete</Button>
            </Col>
          </List.Item>)}
        />
          <Button onClick={e=>this.handleSubmitList(this.props.localLightningList)}>Add</Button>
        </Card>
      </Fragment>
    );
  }
}

export default LightningList;
