import React, {Component, Fragment} from 'react';
import {Form, Input, Button, Card} from 'antd';

class LightningForm extends Component {

  state = {
    subject: "",
    duration: 0,
  };

  handleChange = e => this.setState({
    [e.target.name]: e.target.value
  })

  handleSubmit = e => {
    e.preventDefault();

    let {subject, duration} = this.state;

    // TODO if 60

    if (duration % 5 != 0) {
      //TODO
      duration = Math.ceil(duration / 10) * 10;
    }

   // console.log(duration);

    this.props.handleAddLightning({subject, duration});
    this.setState({subject: "", duration: 0});
  };


  render() {
    return (
      <Fragment>
        <Card>
        <Form>
          <Input name="subject" type="text" placeholder="Subject" value={this.state.subject}
                 onChange={this.handleChange}/>
          <Input name="duration" defaultValue={5} min={5} max={60} step={5} type="number" placeholder="Duration"
                 value={this.state.duration} onChange={this.handleChange}/>
          <Button onClick={this.handleSubmit}>Add</Button>
        </Form>
        </Card>
      </Fragment>
    );
  }
}

export default LightningForm;
