import React, { Component ,Fragment} from 'react';
import {List, Button, Col,Card} from 'antd';


class LightningDetail extends Component {

  render() {
    console.log("details",this.props.lightingList)
    return (
      <Fragment>
        <Card>
          <List
            bordered
            dataSource={this.props.lightningList}
            renderItem={(item, i) => (<List.Item>
              <Col span={12}> {item["starting"]} </Col>
              <Col span={12}> {item.subject} </Col>
              <Col span={4}> {item.duration} min</Col>
              <Col span={4} offset={0}>
                <Button onClick={e => this.handleRemove(i)}>Delete</Button>
              </Col>
            </List.Item>)}
          />
        </Card>
      </Fragment>
    );
  }
}

export default LightningDetail;
